/**
 *     
 * @param {string} dateString -      ISO
 * @returns {string}  
 */
export const formatDate = (dateString) => {
  const date = new Date(dateString);
  return date.toLocaleDateString('ru-RU', { 
    day: '2-digit', 
    month: 'short', 
    year: 'numeric'
  });
};

/**
 *     
 * @param {string} type -  
 * @returns {string}    Material Icons
 */
export const getIconForType = (type) => {
  switch (type) {
    case 'meeting': return 'groups';
    case 'ticket': return 'task';
    case 'wiki': return 'description';
    case 'technical': return 'code';
    case 'product': return 'view_quilt';
    case 'organizational': return 'people';
    default: return 'article';
  }
};

/**
 *     
 * @param {string} category -   
 * @returns {string}   
 */
export const getCategoryName = (category) => {
  const categories = {
    'technical': '',
    'product': '',
    'organizational': '',
    'high': '',
    'medium': '',
    'low': '',
    'open': '',
    'in-progress': ' ',
    'resolved': '',
    'closed': ''
  };
  
  return categories[category] || category;
};

/**
 *        
 * @param {string} text -  
 * @param {number} length -  
 * @returns {string}  
 */
export const truncateText = (text, length = 150) => {
  if (!text) return '';
  return text.length > length ? `${text.substring(0, length)}...` : text;
}; 